.container {
    display: flex;
    align-items: center;
    justify-content: center;
    // height: 100vh;
    a {
      padding: 10px;
      background: teal;
      color: #fff;
      font-weight: bold;
      font-size: 12px;
      border-radius: 10px;
      cursor: pointer;
    }

    a:hover {
        background-color: aqua;
    }
  }
  .modal {
    position: fixed;
    width: 100vw;
    height: 100vh;
    opacity: .50;
    visibility: hidden;
    transition: all 0.3s ease;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  
    &.open {
      visibility: visible;
      opacity: 1;
      transition-delay: 0s;
    }
    &-bg {
      position: absolute;
    //   background: teal;
    //   opacity: .50;
      width: 100%;
      height: 100%;
    }
    &-container {
      border-radius: 10px;
      background: #fff;
      position: relative;
      padding: 30px;
      box-shadow:
        2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
        6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
        12.5px 12.5px 10px rgba(0, 0, 0, 0.035),
        22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
        41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
        100px 100px 80px rgba(0, 0, 0, 0.07);
        width: 50vw;
    }
    &-close {
      position: absolute;
      right: 15px;
      top: 15px;
      outline: none;
      appearance: none;
      color: red;
      background: none;
      border: 0px;
      font-weight: bold;
      cursor: pointer;
    }
  }
